"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "../ui/Card"
import { Button } from "../ui/Button"
import { Input } from "../ui/Input"
import { Label } from "/ui/label"
import { Upload, ExternalLink, Calendar, Settings, Plus, Trash2 } from "lucide-react"

type TestType = "SAT" | "ACT" | "AP Tests"

interface SATScore {
  id: string
  date: string
  reading: number
  math: number
  total: number
}

interface ACTScore {
  id: string
  date: string
  english: number
  math: number
  reading: number
  science: number
  composite: number
}

interface APScore {
  id: string
  subject: string
  score: number
  date: string
}

export default function TestPrepDashboard() {
  const [selectedTest, setSelectedTest] = useState<TestType>("SAT")

  // SAT Scores State
  const [satScores, setSatScores] = useState<SATScore[]>([
    { id: "1", date: "2024-01", reading: 650, math: 700, total: 1350 },
    { id: "2", date: "2024-03", reading: 680, math: 720, total: 1400 },
    { id: "3", date: "2024-05", reading: 700, math: 750, total: 1450 },
  ])

  // ACT Scores State
  const [actScores, setActScores] = useState<ACTScore[]>([
    { id: "1", date: "2024-02", english: 28, math: 30, reading: 26, science: 29, composite: 28 },
    { id: "2", date: "2024-04", english: 30, math: 32, reading: 28, science: 31, composite: 30 },
    { id: "3", date: "2024-06", english: 32, math: 34, reading: 30, science: 33, composite: 32 },
  ])

  // AP Scores State
  const [apScores, setApScores] = useState<APScore[]>([
    { id: "1", subject: "AP Biology", score: 4, date: "2023-05" },
    { id: "2", subject: "AP Calculus AB", score: 5, date: "2023-05" },
    { id: "3", subject: "AP English Literature", score: 3, date: "2024-05" },
    { id: "4", subject: "AP US History", score: 4, date: "2024-05" },
    { id: "5", subject: "AP Chemistry", score: 5, date: "2024-05" },
  ])

  // New score input states
  const [newSATScore, setNewSATScore] = useState({ date: "", reading: "", math: "" })
  const [newACTScore, setNewACTScore] = useState({ date: "", english: "", math: "", reading: "", science: "" })
  const [newAPScore, setNewAPScore] = useState({ subject: "", score: "", date: "" })

  const nextTestDate = "March 9, 2024"

  const resources = [
    { name: "Khan Academy", type: "Free Practice", icon: "ðŸ“š" },
    { name: "College Board SAT Question Bank", type: "Official Questions", icon: "ðŸŽ¯" },
    { name: "Blue Method SAT Prep", type: "Prep Course", icon: "ðŸ“–" },
  ]

  // Add new score functions
  const addSATScore = () => {
    if (newSATScore.date && newSATScore.reading && newSATScore.math) {
      const reading = Number.parseInt(newSATScore.reading)
      const math = Number.parseInt(newSATScore.math)
      const total = reading + math
      const newScore: SATScore = {
        id: Date.now().toString(),
        date: newSATScore.date,
        reading,
        math,
        total,
      }
      setSatScores([...satScores, newScore])
      setNewSATScore({ date: "", reading: "", math: "" })
    }
  }

  const addACTScore = () => {
    if (newACTScore.date && newACTScore.english && newACTScore.math && newACTScore.reading && newACTScore.science) {
      const english = Number.parseInt(newACTScore.english)
      const math = Number.parseInt(newACTScore.math)
      const reading = Number.parseInt(newACTScore.reading)
      const science = Number.parseInt(newACTScore.science)
      const composite = Math.round((english + math + reading + science) / 4)
      const newScore: ACTScore = {
        id: Date.now().toString(),
        date: newACTScore.date,
        english,
        math,
        reading,
        science,
        composite,
      }
      setActScores([...actScores, newScore])
      setNewACTScore({ date: "", english: "", math: "", reading: "", science: "" })
    }
  }

  const addAPScore = () => {
    if (newAPScore.subject && newAPScore.score && newAPScore.date) {
      const newScore: APScore = {
        id: Date.now().toString(),
        subject: newAPScore.subject,
        score: Number.parseInt(newAPScore.score),
        date: newAPScore.date,
      }
      setApScores([...apScores, newScore])
      setNewAPScore({ subject: "", score: "", date: "" })
    }
  }

  // Delete score functions
  const deleteSATScore = (id: string) => {
    setSatScores(satScores.filter((score) => score.id !== id))
  }

  const deleteACTScore = (id: string) => {
    setActScores(actScores.filter((score) => score.id !== id))
  }

  const deleteAPScore = (id: string) => {
    setApScores(apScores.filter((score) => score.id !== id))
  }

  // Get latest scores for circle graphs
  const latestSATScore = satScores[satScores.length - 1]
  const latestACTScore = actScores[actScores.length - 1]

  const renderScoreChart = (scores: any[], maxScore: number, minScore: number, scoreKey: string) => {
    const chartWidth = 500
    const chartHeight = 250
    const padding = 50

    return (
      <div className="relative h-80 mb-6 bg-white rounded-lg border p-6">
        <svg className="w-full h-full" viewBox={`0 0 ${chartWidth} ${chartHeight + 60}`}>
          {/* Y-axis labels and grid lines */}
          {[0, 0.25, 0.5, 0.75, 1].map((ratio, index) => {
            const y = chartHeight - ratio * (chartHeight - padding * 2) - padding
            const scoreValue = Math.round(minScore + (maxScore - minScore) * ratio)
            return (
              <g key={index}>
                <line x1={padding} y1={y} x2={chartWidth - padding} y2={y} stroke="#e5e7eb" strokeWidth="1" />
                <text x={padding - 15} y={y + 4} textAnchor="end" className="text-sm fill-gray-500">
                  {scoreValue}
                </text>
              </g>
            )
          })}

          {/* Score line */}
          {scores.length > 1 && (
            <polyline
              fill="none"
              stroke="#14b8a6"
              strokeWidth="3"
              points={scores
                .map((point, index) => {
                  const x = padding + (index * (chartWidth - padding * 2)) / (scores.length - 1)
                  const scoreValue = scoreKey === "total" ? point.total : point.composite
                  const y =
                    chartHeight -
                    padding -
                    ((scoreValue - minScore) / (maxScore - minScore)) * (chartHeight - padding * 2)
                  return `${x},${y}`
                })
                .join(" ")}
            />
          )}

          {/* Data points */}
          {scores.map((point, index) => {
            const x = padding + (index * (chartWidth - padding * 2)) / Math.max(scores.length - 1, 1)
            const scoreValue = scoreKey === "total" ? point.total : point.composite
            const y =
              chartHeight - padding - ((scoreValue - minScore) / (maxScore - minScore)) * (chartHeight - padding * 2)
            return (
              <g key={index}>
                <circle cx={x} cy={y} r="5" fill="#14b8a6" />
                <text x={x} y={chartHeight + 25} textAnchor="middle" className="text-xs fill-gray-500">
                  {point.date}
                </text>
              </g>
            )
          })}

          {/* X-axis labels */}
          <text x={padding} y={chartHeight + 45} textAnchor="middle" className="text-sm fill-gray-500">
            Initial
          </text>
          <text x={chartWidth - padding} y={chartHeight + 45} textAnchor="middle" className="text-sm fill-gray-500">
            Latest
          </text>

          {/* Settings icon */}
          <g transform={`translate(${chartWidth - 35}, ${chartHeight - 15})`}>
            <rect width="25" height="25" rx="6" fill="#f3f4f6" />
            <Settings className="w-4 h-4" x="12.5" y="12.5" />
          </g>
        </svg>
      </div>
    )
  }

  const renderCircleGraph = (score: number, maxScore: number, label: string, color = "#10d9c4") => {
    const percentage = (score / maxScore) * 100
    return (
      <div className="flex flex-col items-center">
        <div className="relative w-28 h-28 mb-3">
          <svg className="w-28 h-28 transform -rotate-90" viewBox="0 0 36 36">
            <path
              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
              fill="none"
              stroke="#e5e7eb"
              strokeWidth="3"
            />
            <path
              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
              fill="none"
              stroke={color}
              strokeWidth="3"
              strokeDasharray={`${percentage}, 100`}
            />
          </svg>
          <div className="absolute inset-0 flex items-center justify-center">
            <span className="text-xl font-bold text-gray-800">{score}</span>
          </div>
        </div>
        <div className="text-center">
          <div className="text-sm font-medium text-gray-700">{label}</div>
          <div className="text-xs text-gray-500">out of {maxScore}</div>
        </div>
      </div>
    )
  }

  const renderAPBarChart = () => {
    return (
      <div className="space-y-4">
        {apScores.map((ap, index) => (
          <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
            <div className="flex-1">
              <div className="flex justify-between items-center mb-2">
                <span className="text-sm font-medium text-gray-700">{ap.subject}</span>
                <div className="flex items-center gap-2">
                  <span className="text-sm font-bold text-gray-900">{ap.score}/5</span>
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => deleteAPScore(ap.id)}
                    className="h-6 w-6 p-0 text-red-500 hover:text-red-700"
                  >
                    <Trash2 className="w-3 h-3" />
                  </Button>
                </div>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-3">
                <div
                  className="bg-gradient-to-r from-teal-400 to-teal-600 h-3 rounded-full transition-all duration-300"
                  style={{ width: `${(ap.score / 5) * 100}%` }}
                ></div>
              </div>
              <div className="text-xs text-gray-500 mt-1">{ap.date}</div>
            </div>
          </div>
        ))}
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-6xl mx-auto">
        {/* Custom Tab Navigation */}
        <div className="flex gap-1 mb-6 bg-teal-100 p-1 rounded-lg w-fit">
          {(["SAT", "ACT", "AP Tests"] as TestType[]).map((tab) => (
            <button
              key={tab}
              onClick={() => setSelectedTest(tab)}
              className={`px-6 py-2 rounded-md text-sm font-medium transition-all ${
                selectedTest === tab
                  ? "bg-white text-teal-700 shadow-sm"
                  : "text-teal-600 hover:text-teal-700 hover:bg-teal-50"
              }`}
            >
              {tab}
            </button>
          ))}
        </div>

        {/* SAT Content */}
        {selectedTest === "SAT" && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Left Column - Main Content */}
            <div className="lg:col-span-2 space-y-6">
              {/* Add New SAT Score */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">Add SAT Score</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                    <div>
                      <Label htmlFor="sat-date">Test Date</Label>
                      <Input
                        id="sat-date"
                        type="month"
                        value={newSATScore.date}
                        onChange={(e) => setNewSATScore({ ...newSATScore, date: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="sat-reading">Reading (400-800)</Label>
                      <Input
                        id="sat-reading"
                        type="number"
                        min="400"
                        max="800"
                        value={newSATScore.reading}
                        onChange={(e) => setNewSATScore({ ...newSATScore, reading: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="sat-math">Math (400-800)</Label>
                      <Input
                        id="sat-math"
                        type="number"
                        min="400"
                        max="800"
                        value={newSATScore.math}
                        onChange={(e) => setNewSATScore({ ...newSATScore, math: e.target.value })}
                      />
                    </div>
                    <div className="flex items-end">
                      <Button onClick={addSATScore} className="bg-teal-500 hover:bg-teal-600 text-white w-full">
                        <Plus className="w-4 h-4 mr-2" />
                        Add Score
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Test Score Metrics */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">SAT Score Progress</CardTitle>
                  <p className="text-sm text-gray-500">Your SAT score improvement over time</p>
                </CardHeader>
                <CardContent>
                  {renderScoreChart(satScores, 1600, 800, "total")}

                  {/* SAT Section Circles */}
                  <div className="flex justify-center gap-8">
                    {latestSATScore && (
                      <>
                        {renderCircleGraph(latestSATScore.reading, 800, "Reading & Writing", "#3b82f6")}
                        {renderCircleGraph(latestSATScore.math, 800, "Math", "#10d9c4")}
                        {renderCircleGraph(latestSATScore.total, 1600, "Total Score", "#8b5cf6")}
                      </>
                    )}
                  </div>
                </CardContent>
              </Card>

              {/* SAT Score History */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">Score History</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    {satScores.map((score) => (
                      <div key={score.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                        <div className="flex items-center gap-4">
                          <span className="text-sm text-gray-600">{score.date}</span>
                          <span className="text-sm">Reading: {score.reading}</span>
                          <span className="text-sm">Math: {score.math}</span>
                          <span className="text-sm font-semibold">Total: {score.total}</span>
                        </div>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => deleteSATScore(score.id)}
                          className="text-red-500 hover:text-red-700"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Right Column - Next Test and Resources */}
            <div className="space-y-4">
              {/* Next Test Header */}
              <div className="bg-white rounded-lg shadow-sm p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="text-sm font-semibold text-gray-800">Next Test</h3>
                    <div className="flex items-center gap-2 text-gray-600 mt-1">
                      <Calendar className="w-3 h-3" />
                      <span className="text-xs">{nextTestDate}</span>
                    </div>
                  </div>
                  <Button size="sm" className="bg-teal-500 hover:bg-teal-600 text-white text-xs px-3 py-1">
                    Book now
                  </Button>
                </div>
              </div>

              {/* Resources Card */}
              <Card className="bg-white shadow-sm">
                <CardHeader className="pb-3">
                  <CardTitle className="text-lg font-semibold text-gray-800">Resources</CardTitle>
                </CardHeader>
                <CardContent className="space-y-3">
                  {resources.map((resource, index) => (
                    <div
                      key={index}
                      className="flex items-center justify-between p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                    >
                      <div className="flex items-center gap-3">
                        <span className="text-lg">{resource.icon}</span>
                        <div>
                          <div className="font-medium text-gray-800 text-sm">{resource.name}</div>
                          <div className="text-xs text-gray-500">{resource.type}</div>
                        </div>
                      </div>
                      <ExternalLink className="w-4 h-4 text-gray-400" />
                    </div>
                  ))}
                </CardContent>
              </Card>

              {/* Upload Test Card */}
              <Card className="bg-white shadow-sm">
                <CardHeader className="pb-3">
                  <CardTitle className="text-lg font-semibold text-gray-800">Upload Test</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-teal-400 transition-colors">
                    <Upload className="w-8 h-8 text-gray-400 mx-auto mb-3" />
                    <p className="text-sm text-gray-600 mb-2">Let AI analyze your practice tests</p>
                    <p className="text-xs text-gray-500 mb-4">
                      Upload a PDF of your practice test results to get personalized insights and recommendations.
                    </p>
                    <Button
                      variant="outline"
                      size="sm"
                      className="border-teal-500 text-teal-600 hover:bg-teal-50 bg-transparent"
                    >
                      <Upload className="w-4 h-4 mr-2" />
                      Choose File
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        )}

        {/* ACT Content */}
        {selectedTest === "ACT" && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Left Column - Main Content */}
            <div className="lg:col-span-2 space-y-6">
              {/* Add New ACT Score */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">Add ACT Score</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-2 md:grid-cols-6 gap-4">
                    <div>
                      <Label htmlFor="act-date">Test Date</Label>
                      <Input
                        id="act-date"
                        type="month"
                        value={newACTScore.date}
                        onChange={(e) => setNewACTScore({ ...newACTScore, date: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="act-english">English (1-36)</Label>
                      <Input
                        id="act-english"
                        type="number"
                        min="1"
                        max="36"
                        value={newACTScore.english}
                        onChange={(e) => setNewACTScore({ ...newACTScore, english: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="act-math">Math (1-36)</Label>
                      <Input
                        id="act-math"
                        type="number"
                        min="1"
                        max="36"
                        value={newACTScore.math}
                        onChange={(e) => setNewACTScore({ ...newACTScore, math: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="act-reading">Reading (1-36)</Label>
                      <Input
                        id="act-reading"
                        type="number"
                        min="1"
                        max="36"
                        value={newACTScore.reading}
                        onChange={(e) => setNewACTScore({ ...newACTScore, reading: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="act-science">Science (1-36)</Label>
                      <Input
                        id="act-science"
                        type="number"
                        min="1"
                        max="36"
                        value={newACTScore.science}
                        onChange={(e) => setNewACTScore({ ...newACTScore, science: e.target.value })}
                      />
                    </div>
                    <div className="flex items-end">
                      <Button onClick={addACTScore} className="bg-teal-500 hover:bg-teal-600 text-white w-full">
                        <Plus className="w-4 h-4 mr-2" />
                        Add
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Test Score Metrics */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">ACT Score Progress</CardTitle>
                  <p className="text-sm text-gray-500">Your ACT score improvement over time</p>
                </CardHeader>
                <CardContent>
                  {renderScoreChart(actScores, 36, 10, "composite")}

                  {/* ACT Section Circles */}
                  <div className="grid grid-cols-2 md:grid-cols-5 gap-4 justify-items-center">
                    {latestACTScore && (
                      <>
                        {renderCircleGraph(latestACTScore.english, 36, "English", "#3b82f6")}
                        {renderCircleGraph(latestACTScore.math, 36, "Math", "#10d9c4")}
                        {renderCircleGraph(latestACTScore.reading, 36, "Reading", "#f59e0b")}
                        {renderCircleGraph(latestACTScore.science, 36, "Science", "#ef4444")}
                        {renderCircleGraph(latestACTScore.composite, 36, "Composite", "#8b5cf6")}
                      </>
                    )}
                  </div>
                </CardContent>
              </Card>

              {/* ACT Score History */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">Score History</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    {actScores.map((score) => (
                      <div key={score.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                        <div className="flex items-center gap-4 flex-wrap">
                          <span className="text-sm text-gray-600">{score.date}</span>
                          <span className="text-sm">E: {score.english}</span>
                          <span className="text-sm">M: {score.math}</span>
                          <span className="text-sm">R: {score.reading}</span>
                          <span className="text-sm">S: {score.science}</span>
                          <span className="text-sm font-semibold">Composite: {score.composite}</span>
                        </div>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => deleteACTScore(score.id)}
                          className="text-red-500 hover:text-red-700"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Right Column - Next Test and Resources */}
            <div className="space-y-4">
              {/* Next Test Header */}
              <div className="bg-white rounded-lg shadow-sm p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="text-sm font-semibold text-gray-800">Next Test</h3>
                    <div className="flex items-center gap-2 text-gray-600 mt-1">
                      <Calendar className="w-3 h-3" />
                      <span className="text-xs">{nextTestDate}</span>
                    </div>
                  </div>
                  <Button size="sm" className="bg-teal-500 hover:bg-teal-600 text-white text-xs px-3 py-1">
                    Book now
                  </Button>
                </div>
              </div>

              {/* Resources Card */}
              <Card className="bg-white shadow-sm">
                <CardHeader className="pb-3">
                  <CardTitle className="text-lg font-semibold text-gray-800">Resources</CardTitle>
                </CardHeader>
                <CardContent className="space-y-3">
                  {resources.map((resource, index) => (
                    <div
                      key={index}
                      className="flex items-center justify-between p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                    >
                      <div className="flex items-center gap-3">
                        <span className="text-lg">{resource.icon}</span>
                        <div>
                          <div className="font-medium text-gray-800 text-sm">{resource.name}</div>
                          <div className="text-xs text-gray-500">{resource.type}</div>
                        </div>
                      </div>
                      <ExternalLink className="w-4 h-4 text-gray-400" />
                    </div>
                  ))}
                </CardContent>
              </Card>

              {/* Upload Test Card */}
              <Card className="bg-white shadow-sm">
                <CardHeader className="pb-3">
                  <CardTitle className="text-lg font-semibold text-gray-800">Upload Test</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-teal-400 transition-colors">
                    <Upload className="w-8 h-8 text-gray-400 mx-auto mb-3" />
                    <p className="text-sm text-gray-600 mb-2">Let AI analyze your practice tests</p>
                    <p className="text-xs text-gray-500 mb-4">
                      Upload a PDF of your practice test results to get personalized insights and recommendations.
                    </p>
                    <Button
                      variant="outline"
                      size="sm"
                      className="border-teal-500 text-teal-600 hover:bg-teal-50 bg-transparent"
                    >
                      <Upload className="w-4 h-4 mr-2" />
                      Choose File
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        )}

        {/* AP Tests Content */}
        {selectedTest === "AP Tests" && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Left Column - Main Content */}
            <div className="lg:col-span-2 space-y-6">
              {/* Add New AP Score */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">Add AP Score</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                    <div>
                      <Label htmlFor="ap-subject">Subject</Label>
                      <Input
                        id="ap-subject"
                        placeholder="e.g., AP Biology"
                        value={newAPScore.subject}
                        onChange={(e) => setNewAPScore({ ...newAPScore, subject: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="ap-score">Score (1-5)</Label>
                      <Input
                        id="ap-score"
                        type="number"
                        min="1"
                        max="5"
                        value={newAPScore.score}
                        onChange={(e) => setNewAPScore({ ...newAPScore, score: e.target.value })}
                      />
                    </div>
                    <div>
                      <Label htmlFor="ap-date">Test Date</Label>
                      <Input
                        id="ap-date"
                        type="month"
                        value={newAPScore.date}
                        onChange={(e) => setNewAPScore({ ...newAPScore, date: e.target.value })}
                      />
                    </div>
                    <div className="flex items-end">
                      <Button onClick={addAPScore} className="bg-teal-500 hover:bg-teal-600 text-white w-full">
                        <Plus className="w-4 h-4 mr-2" />
                        Add Score
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* AP Test Scores */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">AP Test Scores</CardTitle>
                  <p className="text-sm text-gray-500">Your AP exam results by subject</p>
                </CardHeader>
                <CardContent>{renderAPBarChart()}</CardContent>
              </Card>

              {/* AP Score Summary */}
              <Card className="bg-white shadow-sm">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold text-gray-800">Score Summary</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-5 gap-4 text-center">
                    {[1, 2, 3, 4, 5].map((score) => {
                      const count = apScores.filter((ap) => ap.score === score).length
                      return (
                        <div key={score} className="p-4 bg-gray-50 rounded-lg">
                          <div className="text-2xl font-bold text-gray-800">{count}</div>
                          <div className="text-sm text-gray-600">Score {score}</div>
                        </div>
                      )
                    })}
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Right Column - Next Test and Resources */}
            <div className="space-y-4">
              {/* Next Test Header */}
              <div className="bg-white rounded-lg shadow-sm p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="text-sm font-semibold text-gray-800">Next Test</h3>
                    <div className="flex items-center gap-2 text-gray-600 mt-1">
                      <Calendar className="w-3 h-3" />
                      <span className="text-xs">May 6-17, 2024</span>
                    </div>
                  </div>
                  <Button size="sm" className="bg-teal-500 hover:bg-teal-600 text-white text-xs px-3 py-1">
                    Register
                  </Button>
                </div>
              </div>

              {/* Resources Card */}
              <Card className="bg-white shadow-sm">
                <CardHeader className="pb-3">
                  <CardTitle className="text-lg font-semibold text-gray-800">Resources</CardTitle>
                </CardHeader>
                <CardContent className="space-y-3">
                  {[
                    { name: "AP Classroom", type: "Official Resources", icon: "ðŸŽ“" },
                    { name: "College Board AP Practice", type: "Practice Exams", icon: "ðŸ“" },
                    { name: "AP Study Guides", type: "Review Materials", icon: "ðŸ“š" },
                  ].map((resource, index) => (
                    <div
                      key={index}
                      className="flex items-center justify-between p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                    >
                      <div className="flex items-center gap-3">
                        <span className="text-lg">{resource.icon}</span>
                        <div>
                          <div className="font-medium text-gray-800 text-sm">{resource.name}</div>
                          <div className="text-xs text-gray-500">{resource.type}</div>
                        </div>
                      </div>
                      <ExternalLink className="w-4 h-4 text-gray-400" />
                    </div>
                  ))}
                </CardContent>
              </Card>

              {/* Upload Test Card */}
              <Card className="bg-white shadow-sm">
                <CardHeader className="pb-3">
                  <CardTitle className="text-lg font-semibold text-gray-800">Upload Test</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-teal-400 transition-colors">
                    <Upload className="w-8 h-8 text-gray-400 mx-auto mb-3" />
                    <p className="text-sm text-gray-600 mb-2">Upload AP practice tests</p>
                    <p className="text-xs text-gray-500 mb-4">
                      Get detailed analysis of your AP practice test performance and targeted study recommendations.
                    </p>
                    <Button
                      variant="outline"
                      size="sm"
                      className="border-teal-500 text-teal-600 hover:bg-teal-50 bg-transparent"
                    >
                      <Upload className="w-4 h-4 mr-2" />
                      Choose File
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
